import { Greeting } from "./components/Greeting"
import { Weather, type WeatherData } from "./components/Weather"
import { Counter } from "./components/Counter"

// Declare the SSR config provided by Rust (only available during SSR)
declare const __SSR_CONFIG__:
    | {
          weatherApiUrl: string
          clientChunks: string[]
      }
    | undefined

interface AppProps {
    weather?: WeatherData | null
    weatherError?: string
}

export function App({ weather, weatherError }: AppProps) {
    const tempC = weather?.tempC ?? 0

    // Get client chunks from SSR config, or use fallback for client-side
    const chunks =
        typeof __SSR_CONFIG__ !== "undefined"
            ? __SSR_CONFIG__.clientChunks
            : ["client.js"]

    return (
        <html>
            <head>
                <meta charSet="utf-8" />
                <title>Hello World - React SSR</title>
            </head>
            <body>
                <Greeting name="World" />
                <p>This was rendered server-side with React and Deno!</p>
                <hr />
                <Weather data={weather ?? null} error={weatherError} />
                <hr />
                <Counter initialValue={tempC} />

                {/* Embed initial state for hydration */}
                <script
                    dangerouslySetInnerHTML={{
                        __html: `window.__INITIAL_STATE__ = ${JSON.stringify({ weather, weatherError })}`,
                    }}
                />
                {/* Load all client chunks (vendor first, then app) */}
                {chunks.map((chunk) => (
                    <script key={chunk} src={`/dist/${chunk}`} />
                ))}
            </body>
        </html>
    )
}
